import react, {Component} from "react";
import EduItem from './EduItem';

class Education extends Component{
    render(){
        return(
            <div className = "container bg-light mt-5 rounded ">
            
            <text className = "container display-4 text-left text-info d-block p-4">Education</text>
            
            <div className = "container bg-light p-3 mt-2">
            <div className = "row border">
            <EduItem></EduItem>
            </div>

            <div className = "row border bg-info mt-2">
            <EduItem prog = "HSC"></EduItem>
            </div>

            </div>
            

            </div>
        );
    }
}
export default Education;