import React, {Component} from "react"
import MyPhoto from "../Images/Photo.jpg"

import {FaGithub, FaLinkedin} from "react-icons/fa"

class LeftView extends Component{
    render(){
        return(
            <div className = "grid-container">
            <div className = "row m-2 justify-content-center">
            <img src={MyPhoto} className = ' mt-3 rounded-circle w-75'></img>
            </div>
            <div className = "row justify-content-center mt-5">
            <FaGithub className = "display-4 text-light rounded-circle"></FaGithub>
            </div>
            <div className = "row m-2 justify-content-center mt-4">
            <FaLinkedin className = "display-4 text-light rounded"></FaLinkedin>
            </div>
            </div>
        );
    }
}

export default LeftView;