import React, {Component} from "react"
import MyPhoto from "../Images/Md. Sakib Khan.jpg"

import {FaGithub, FaLinkedin} from "react-icons/fa"

class LeftView extends Component{
    render(){
        return(
                <div className = "grid-container ">
                <div class="row p-4">
                <div className = "col-sm-2 bg-light border-bottom">
                <button className = "btn btn-lg btn-light p-3" onClick = {this.props.Changed} value = "Dashboard">Dashboard</button>
                </div>
                <div className = "col-sm-2  bg-light border-bottom">
                <button className = "btn btn-lg btn-light p-3 " onClick = {this.props.Changed} value = "Education">Education</button>
                </div>
                <div className = "col-sm-2  bg-light border-bottom">
                <button className = "btn btn-lg btn-light p-3 " onClick = {this.props.Changed} value = "Work History">Work History</button>
                </div>
                <div className = "col-sm-2  bg-light border-bottom">
                <button className = "btn btn-lg btn-light p-3 "  onClick = {this.props.Changed} value = "Skills">Skills</button>
                </div>
                <div className = "col-sm-2  bg-light border-bottom">
                <button className = "btn btn-lg btn-light p-3 " onClick = {this.props.Changed} value = "Thesis">Thesis</button>
                </div>
                <div className = "col-sm-2  bg-light border-bottom">
                <button className = "btn btn-lg btn-light p-3" onClick = {this.props.Changed} value = "Project">Project</button>
                </div>
                </div>
                </div>

        );
    }
}

export default LeftView;