import React, {Component} from "react"
import './RightBar.css'
import Education from "./Education"

class RightView extends Component{

    render(){
        
        if (this.props.currentState == "Dashboard") {
                return(
                <div className = "grid-container mb-5">
                <div className = "align-items-lg-center">
                <text className = "display-1 d-block align-self-center " id = "hi">HI, I am Sakib</text>
                <text className = "display-4 d-block align-self-center " id = "hi">This Website is under Development.</text>
                </div>
                </div> 
            );
        }
        if (this.props.currentState == "Education") {
                return(
                <div className = "grid-container">
                <Education></Education>
                </div> 
            );
        }
    }
}

export default RightView;